<template>
  <div
    v-loading="loading"
    class="content-analysis-upload"
  >
    <gs-upload
      type="dragger"
      multiple
      action="http://gs-ui.gridsum.com:8888/upload"
    >
      <gs-icon name="cloud-upload" />
      <div class="gs-upload-dragger-tip">
        （可点击或拖拽文件至此，支持.csv, .xls, .xlsx格式，最大100M）
      </div>
    </gs-upload>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false
    };
  }
};
</script>




