<template>
  <gs-steps
    v-bind="stepsProps"
    :active="active"
    align-center
  >
    <template slot="header">
      <gs-step
        v-for="(item, i) in steps"
        :key="i"
        :title="item.title || item"
      />
    </template>
    <slot slot="content" />
    <template slot="footer">
      <gs-button
        class="gs-steps-prev-step"
        @click="handleChange(-1)"
      >上一步</gs-button>
      <gs-button
        class="gs-steps-next-step"
        type="primary"
        @click="handleChange(1)"
      >下一步</gs-button>
      <!-- <gs-button
        v-if="active !== 0 && active !== total"
        class="gs-steps-prev-step"
        @click="handleChange(-1)"
      >上一步</gs-button>
      <gs-button
        v-if="active !== 0 && active !== total"
        class="gs-steps-next-step"
        type="primary"
        @click="handleChange(1)"
      >下一步</gs-button> -->
    </template>
  </gs-steps>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 0
    },
    steps: {
      type: Array,
      default() {
        return [];
      }
    },
    stepsProps: {
      type: Object,
      default() {

      }
    }
  },

  data() {
    return {
      active: this.value
    };
  },

  computed: {
    total() {
      return this.steps.length - 1;
    }
  },

  watch: {
    value(val) {
      this.active = val;
    }
  },

  methods: {
    handleChange(step) {
      this.active = this.active + step;
      this.$emit('input', this.active);
      this.$emit('change', this.active);
    }
  }
};
</script>




