<template>
  <div class="content-analysis-setting">
    <gs-tabs
      v-model="active"
      type="content-tab"
    >
      <gs-tab-pane
        v-for="(item, i) in dataSources"
        :key="i"
        :name="`${i}`"
      >
        <div slot="label">
          {{ item.label }}
          <el-tag
            v-if="item.isNew"
            type="success"
            size="mini"
          >新建表</el-tag>
        </div>
        <table-setting-item />
      </gs-tab-pane>
    </gs-tabs>
  </div>
</template>

<script>
import TableSettingItem from './table-setting-item';

export default {
  components: {
    TableSettingItem
  },

  data() {
    return {
      active: '0',
      dataSources: [
        {
          label: '数据源1'
        },
        {
          label: '数据源2',
          isNew: true
        }
      ]
    };
  }
};
</script>




