<template>
  <div class="content-analysis-target">
    <h3 class="target-title">设置目标表</h3>
    <el-table :data="dataSources">
      <el-table-column
        prop="source"
        label="数据源"
        width="180"
      />
      <el-table-column
        prop="target"
        label="目标库"
        width="180"
      >
        <template slot-scope="scope">
          <gs-select
            v-model="scope.row.target"
            placeholder="请选择数据库"
            @change="handleColumnInput(scope)"
          >
            <gs-option
              v-for="(item, index) in databases"
              :key="index"
              :label="item"
              :value="item"
            />
          </gs-select>
        </template>
      </el-table-column>
      <el-table-column
        prop="targetTable"
        label="目标表"
      >
        <template slot-scope="scope">
          <gs-input
            v-model="scope.row.targetTable"
            placeholder="请输入字段描述"
            @change="handleColumnInput(scope)"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="action"
        label="数据库操作"
      >
        <template slot-scope="scope">
          <gs-select
            v-model="scope.row.action"
            placeholder="请选择数据库"
            @change="handleColumnInput(scope)"
          >
            <gs-option
              v-for="(item, index) in databases"
              :key="index"
              :label="item"
              :value="item"
            />
          </gs-select>
        </template>
      </el-table-column>
    </el-table>
    <gs-alert
      :closable="false"
      :show-icon="false"
    >
      说明：在这里配置对上传文件的操作，包括创建新表和更新现有表。系统会根据输入的目标表名自动判断操作类型。
      更新操作具体包括追加（不影响现有数据）和覆盖（删除现有数据）。对一个目标表， 目前仅支持意向操作。
    </gs-alert>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dataSources: [
        {
          source: '数据源1',
          target: '',
          targetTable: '',
          action: ''
        }
      ],
      databases: []
    };
  },

  methods: {
    handleColumnInput() {

    }
  }
};
</script>




